  .search-bar input {
    font-size: 13px;
  }
  .search-dropdown {
    max-height: 300px;
  }
  .search-result-item {
    padding: 10px 12px;
  }
  .search-result-icon {
    width: 32px;
    height: 32px;
    font-size: 14px;
  }
}
